img {
  max-width: 100%;
  display: block;
}
p {
  margin: 0;
}
h2 {
  margin-top: 0;
}
.container {
  max-width: 747px;
  margin: 0 auto;
  padding: 10px;
  font-family: 'Raleway', sans-serif;
}
.breed-select {
  text-align: center;
  display: block;
  margin: 0 auto 15px;
}
.title {
  display: block;
  margin: 0 auto 10px;
  text-align: center;
  max-width: 500px;
}
.loaderP {
  font-size: 20px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.choices__item::after {
  display: none;
}
.card-title {
  font-size: 30px;
  margin-bottom: 30px;
}
.card-description {
  font-size: 20px;
  margin-bottom: 20px;
}
.card-special {
  font-size: 20px;
}
.card-special-s {
  font-weight: bold;
}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1980px;
  }
  .card-container {
    display: flex;
    column-gap: 30px;
  }
  .card-img {
    width: 500px;
    object-fit: contain;
  }
  .card-info {
    align-self: center;
  }
}
.loader {
  margin-right: 25px;
  display: inline-block;
  color: #000;
  font-size: 45px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  transform: translateZ(0);
  animation: mltShdSpin 1.7s infinite ease, round 1.7s infinite ease;
}

@keyframes mltShdSpin {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em,
      -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em,
      -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em,
      -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em,
      -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em,
      -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em,
      -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}

@keyframes round {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
